/* Responsive styles for catalog */

/* Mobile styles (up to 767px) */
@media (max-width: 767px) {
  .catalog-container {
    padding: 10px;
  }
  
  .catalog-header h1 {
    font-size: 1.5rem;
    margin-bottom: 15px;
  }
  
  .catalog-actions-center {
    flex-direction: column;
    align-items: center;
    gap: 10px;
    width: 100%;
  }
  
  .catalog-actions-center button,
  .catalog-actions-center .export-dropdown {
    width: 100%;
    margin: 0;
  }
  
  .display-mode-selector {
    width: 100%;
    margin-top: 10px;
  }
  
  .display-mode-button {
    flex: 1;
  }
  
  .catalog-filters {
    flex-direction: column;
  }
  
  .search-filter {
    width: 100%;
    margin-bottom: 10px;
  }
  
  .filter-group {
    width: 100%;
    flex-direction: column;
  }
  
  .filter-item {
    width: 100%;
    margin-bottom: 10px;
  }
  
  .filter-dropdown {
    width: 100%;
  }
  
  .rubrics-container {
    margin-top: 15px;
  }
  
  .rubrics-buttons {
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 5px;
  }
  
  .rubric-button {
    font-size: 0.8rem;
    padding: 5px 10px;
    margin: 2px;
  }
  
  .tags-filter-container {
    margin-top: 15px;
  }
  
  .tags-buttons {
    flex-wrap: wrap;
    gap: 5px;
  }
  
  .tag-button {
    font-size: 0.8rem;
    padding: 3px 8px;
    margin: 2px;
  }
  
  .prompts-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .prompt-card {
    margin-bottom: 15px;
  }
  
  .pagination {
    flex-wrap: wrap;
    justify-content: center;
    gap: 5px;
  }
  
  .pagination-button {
    font-size: 0.8rem;
    padding: 5px 8px;
    margin: 0 2px;
  }
  
  .modal-content {
    width: 95%;
    max-width: 95%;
    max-height: 90vh;
    padding: 15px;
  }
  
  .modal-header h2 {
    font-size: 1.2rem;
  }
  
  .prompt-details {
    flex-direction: column;
  }
  
  .prompt-text, .prompt-info {
    width: 100%;
  }
  
  .bulk-actions {
    padding: 10px;
  }
  
  .bulk-controls {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .bulk-select-all, .bulk-category-select, .bulk-status-select, .bulk-delete {
    width: 100%;
    margin: 5px 0;
  }
  
  /* Новые стили для выпадающих списков на мобильных устройствах */
  .rubrics-dropdown,
  .tags-dropdown-container {
    width: 100%;
  }
  
  .rubrics-dropdown select,
  .filter-dropdown select {
    width: calc(100% - 40px);
  }
  
  .category-actions,
  .status-actions {
    display: flex;
    gap: 5px;
  }
  
  .small-button {
    padding: 2px 6px;
    font-size: 0.9rem;
  }
}

/* Tablet styles (768px - 1023px) */
@media (min-width: 768px) and (max-width: 1023px) {
  .catalog-container {
    padding: 15px;
  }
  
  .catalog-actions-center {
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
  }
  
  .prompts-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }
  
  .filter-group {
    flex-wrap: wrap;
  }
  
  .filter-item {
    width: calc(50% - 10px);
    margin-bottom: 10px;
  }
  
  .modal-content {
    width: 90%;
    max-width: 90%;
  }
  
  /* Новые стили для выпадающих списков на планшетах */
  .rubrics-dropdown,
  .tags-dropdown-container {
    width: 100%;
  }
  
  .rubrics-dropdown select {
    width: calc(100% - 40px);
  }
}

/* Desktop styles (1024px and above) */
@media (min-width: 1024px) {
  .catalog-container {
    padding: 20px;
  }
  
  .catalog-actions-center {
    justify-content: center;
    gap: 15px;
  }
  
  .prompts-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
  
  .filter-group {
    justify-content: flex-start;
    gap: 20px;
  }
  
  .filter-item {
    width: auto;
  }
  
  .modal-content {
    width: 80%;
    max-width: 1000px;
  }
  
  /* Новые стили для выпадающих списков на десктопе */
  .rubrics-dropdown,
  .tags-dropdown-container {
    display: flex;
    align-items: center;
  }
  
  .rubrics-dropdown select {
    min-width: 200px;
  }
}

/* Общие стили для всех устройств */
.catalog-actions-center {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  width: 100%;
}

.filter-dropdown {
  display: flex;
  align-items: center;
}

.category-actions,
.status-actions {
  display: flex;
  margin-left: 5px;
}

.small-button {
  background-color: var(--accent-color);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 5px;
}

.small-button:hover {
  opacity: 0.9;
}

.add-button {
  background-color: #4caf50;
}

.edit-button {
  background-color: #2196f3;
}

.delete-button {
  background-color: #f44336;
}

.rubrics-dropdown,
.tags-dropdown-container {
  margin-top: 10px;
}

.rubrics-dropdown select,
.filter-dropdown select {
  padding: 8px 12px;
  border-radius: 4px;
  border: 1px solid #ccc;
  background-color: white;
  font-size: 0.9rem;
}

.tags-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 10px;
}

.tag-button.show-more {
  background-color: #e0e0e0;
  color: #333;
}

.tag-button.add-tag {
  background-color: #4caf50;
  color: white;
}

.display-mode-selector {
  display: flex;
  align-items: center;
  gap: 5px;
}

.display-mode-button {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 6px 12px;
  border: 1px solid #ccc;
  background-color: white;
  border-radius: 4px;
  cursor: pointer;
}

.display-mode-button.active {
  background-color: var(--accent-color);
  color: white;
  border-color: var(--accent-color);
}

.mode-icon {
  font-size: 1.2rem;
}

/* Стили для темной темы */
.dark-theme .filter-dropdown select,
.dark-theme .rubrics-dropdown select {
  background-color: #333;
  color: white;
  border-color: #555;
}

.dark-theme .display-mode-button {
  background-color: #333;
  color: #ddd;
  border-color: #555;
}

.dark-theme .display-mode-button.active {
  background-color: var(--accent-color);
  color: white;
}

.dark-theme .tag-button.show-more {
  background-color: #444;
  color: #ddd;
}
